import React from "react";

function MissionKey() {
  return (
    <div className="my-3">
      <p>
        <span className="px-3 mr-2 bg-success" />
        =success
      </p>
      <p>
        <span className="px-3 mr-2 bg-danger" />
        =Fail
      </p>
    </div>
  );
}

export default MissionKey;
